___CSS_LOADER_EXPORT___.push([module.id, "\n.selected-dev-count {\n        background-color: #FFFFFF;\n        border-radius: 4px;\n        border: 1px solid #BCBCBC;\n        color: #212121;\n        padding-left: 12px;\n        height: 40px;\n        display: flex;\n        align-items: center;\n}\n.custom-select .items .option {\n        font-size: 14px;\n        padding-left: 12px;\n}\n.custom-select .items .option:hover {\n        background-color: #EEEEEE;\n        color: #212121;\n}\n.custom-select .items {\n        color: #212121;\n        border-radius: 8px;\n        border: 1px solid #E3E3E3;\n        overflow: hidden;\n        background-color: #FFFFFF;\n        box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/dialogs/premium/premiumAccountForm.vue"],"names":[],"mappings":";AAmKI;QACI,yBAAyB;QACzB,kBAAkB;QAClB,yBAAyB;QACzB,cAAc;QACd,kBAAkB;QAClB,YAAY;QACZ,aAAa;QACb,mBAAmB;AACvB;AACA;QACI,eAAe;QACf,kBAAkB;AACtB;AACA;QACI,yBAAyB;QACzB,cAAc;AAClB;AACA;QACI,cAAc;QACd,kBAAkB;QAClB,yBAAyB;QACzB,gBAAgB;QAChB,yBAAyB;QACzB,0CAA0C;AAC9C","sourcesContent":["<script setup lang=\"ts\">\n    import { container } from \"tsyringe\";\n    import { computed, defineAsyncComponent, ref } from \"vue\";\n    import { IDialog } from \"../../../../../domain/presentation/IDialog\";\n    import CustomSelect from \"../../../components/customSelect.vue\"\n    import { IUserUseCase } from \"../../../../../domain/useCase/IUserUseCase\";\n    import { IUseCaseExecutor } from \"../../../../../domain/useCase/IUseCaseExecutor\";\n    \n    interface IUserRequestProp {\n        orgID: string;\n    }\n    const useCaseExecutor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\n    const userUseCase = container.resolve<IUserUseCase>(IUserUseCase);\n    const dialog = container.resolve<IDialog>(IDialog);\n    const props = defineProps<IUserRequestProp>();\n    const emit = defineEmits([\"close\"]);\n    const companyName = ref(\"\");\n    const phoneNumber = ref(\"\");\n    const orgDevCount = ref(\"\");\n\n    function close() {\n        emit(\"close\");\n    }\n    function phoneMask() {\n        let phoneNumberInput = document.getElementById(\"phone-number\") as HTMLInputElement;\n        if(phoneNumberInput.value.length > 0) {\n            let phoneNum = phoneNumberInput.value.replace(/\\D/g,'');\n            phoneNum = phoneNum.substring(0,1) + '(' + phoneNum.substring(1,4) + ')' + '-' + phoneNum.substring(4,7) + '-' + phoneNum.substring(7,11);\n            phoneNumberInput.value = phoneNum;\n        }\n\n    }\n    function setOrgDevCount(count: string) {\n        orgDevCount.value = count;\n    }\n    async function submit() {\n        await useCaseExecutor.execute(async () => {\n            await userUseCase.createUserPremiumAccountRequest({ companyName: companyName.value, orgDevCount: orgDevCount.value, phoneNumber: phoneNumber.value, sentOrganizationId: props.orgID });\n        }, { loading: true });\n        close();\n        dialog.showDialog(\n            defineAsyncComponent(() => import(`./premiumFormSubmitted.vue`)),\n            { closable: true, image: {src: \"https://stechq.github.io/cdn/Idetemplates/plateauLogo-premium.svg\", width: \"134px\", height: \"45px\"}, \n            width: \"1022px\", maxHeight: \"688px\", customStyle: {headerStyle: \"background: #1B4874; border-bottom: 1px solid #E3E3E3; padding: 20px; height: 80px; display: flex; align-items: center;\", closeIcon: \"color: #FFFFFF\"} }\n        );\n    }\n\n    const isDisabled = computed(() => !companyName.value || !orgDevCount.value || (phoneNumber.value && phoneNumber.value.length < 15))\n</script>\n<template>\n    <div class=\"container\">\n        <div style=\"margin-bottom: 40px;\">\n            <p style=\"font-family: 'Inter';font-size: 40px; font-weight: 700;line-height: 48px; color: #F1AE00;\">One Step Closer to Plateau Studio Premium</p>\n            <p style=\"font-family: 'Roboto';font-size: 20px; line-height: 23px; color: #FFFFFF;\">You can implement best user development on Plateau Studio Premium’s unique dev experience. Fill out this form and we’ll get back to you shortly.</p>\n        </div> \n        <div style=\"display: flex;\">\n            <div>\n                <img src=\"../../../../../images/premium-form-image.svg\" style=\"margin-right: 40px; width: 470px\"/>\n            </div>\n            <div style=\"margin-top: 80px;\">\n                <label class=\"label-text\">Company Information\n                    <span class=\"company-name-info\">\n                        <i class=\"mdi mdi-information-outline\" style=\"font-size: 18px\"></i>\n                        <span class=\"button-tooltip\" style=\"width: 231px;\">Please enter the name of the company to which this application belongs, or the company that requires licensing for the premium pass.</span>\n                    </span>\n                </label>\n                <input class=\"form-input\" placeholder=\"Enter Company Name\" v-model=\"companyName\"/>\n                <label class=\"label-text\">Organization Developer Count</label>\n                <CustomSelect :options=\"['1', '2 - 5', '5 - 10', '> 10']\" class=\"select-dev-count\" :placeholder=\"'Please Select'\" :selectedClass=\"'selected-dev-count'\" @input=\"setOrgDevCount\"></CustomSelect>\n                <label class=\"label-text\">Phone Number</label>\n                <input class=\"form-input\" id=\"phone-number\" placeholder=\"0(5XX)-XXX-XXXX\" @keyup=\"phoneMask()\" maxLength=\"15\" minLength=\"15\" v-model=\"phoneNumber\"/>\n            </div>\n        </div>\n        <div class=\"buttons-div\">\n            <button class=\"cancel-button\" @click=\"close()\">Cancel</button>\n            <button id=\"sendButton\" class=\"submit-form-button\" :disabled=\"isDisabled\" @click=\"submit()\">SUBMIT</button>\n        </div>\n    </div>\n</template>\n\n<style scoped>\n    .container {\n        padding: 32px 40px 24px;\n        background: #1B4874\n    }\n    .label-text {\n        color: #FFFFFF;\n        font-family: Roboto;\n        font-size: 15px;\n        font-weight: 500;\n        line-height: 18px;\n        width: 100%;\n        margin-bottom: 4px;\n\n    }\n    .form-input {\n            background: #FFFFFF;\n        color: #212121;\n        width: 100%;\n        height: 40px;\n        margin-bottom: 20px;\n        border: 1px solid #BCBCBC;\n        padding: 8px 8px 8px 12px;\n        border-radius: 4px;\n    }\n    .form-input:focus-visible {\n        outline: none;\n    }\n    .select-dev-count {\n        margin-bottom: 20px;\n    }\n    .buttons-div {\n        display: flex;\n        justify-content: end;\n        margin-top: 52px;\n    }\n    .cancel-button {\n        font-weight: 500;\n        font-size: 16px;\n        text-align: center;\n        color: #FFFFFF;\n        display: flex;\n        align-items: center;\n        padding: 8px 16px;\n        border-radius: 4px;\n        margin-right: 4px;\n        border: none;\n        background: transparent;\n    }\n    .submit-form-button {\n        font-weight: 500;\n        font-size: 16px;\n        text-align: center;\n        color: #151233;\n        background: #FFFFFF;\n        display: flex;\n        align-items: center;\n        padding: 8px 16px;\n        border-radius: 4px;\n        border: none;\n    }\n    .submit-form-button:disabled {\n        opacity: 50%;\n    }\n    .company-name-info:hover .button-tooltip {\n        visibility: visible;\n        opacity: 1;\n    }\n    .button-tooltip {\n        visibility: hidden;\n        background: #757575;\n        color: #FFFFFF;\n        font-size: 11px;\n        font-family: 'Roboto';\n        text-align: center;\n        border-radius: 4px;\n        letter-spacing: 0.2px;\n        padding: 6px 8px 6px 8px;\n        opacity: 0;\n        position: absolute;\n    }\n</style>\n<style>\n    .selected-dev-count {\n        background-color: #FFFFFF;\n        border-radius: 4px;\n        border: 1px solid #BCBCBC;\n        color: #212121;\n        padding-left: 12px;\n        height: 40px;\n        display: flex;\n        align-items: center;\n    }\n    .custom-select .items .option {\n        font-size: 14px;\n        padding-left: 12px;\n    }\n    .custom-select .items .option:hover {\n        background-color: #EEEEEE;\n        color: #212121;\n    }\n    .custom-select .items {\n        color: #212121;\n        border-radius: 8px;\n        border: 1px solid #E3E3E3;\n        overflow: hidden;\n        background-color: #FFFFFF;\n        box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n    }\n</style>"],"sourceRoot":""}]);
